.registration-listnear {
    background-color: #f4f8ff;
    .registration-listnear-wrapper {
        padding: 160px 140px 100px 50px;
        display: flex;
        flex-direction: column;
        h5 {
            font-weight: bold;
            font-size: 22px;
            color: #000000;
            margin: 30px 0 20px 0;
        }
        .form-wrapper {
            display: flex;
            flex-direction: column;
            .form {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                .form-first,
                .form-last {
                    width: 50%;
                    ul {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-end;
                        li {
                            margin-bottom: 20px;
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            justify-content: flex-end;
                            width: 100%;
                            input {
                                width: 50%;
                            }
                            .category {
                                width: 50%;
                            }
                            .input-date {
                                height: 40px;
                                border-radius: 10px;
                                background-color: #ffffff;
                                box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
                                padding: 20px;
                            }
                        }
                        label {
                            font-size: 16px;
                            line-height: 29px;
                            color: #222d44;
                            margin-right: 20px;
                        }
                        .error{
                            color: red;
                            width: 265px;
                            margin: -10px 0 10px 0;
                        }
                    }
                }
            }
            .button-submit{
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
            }
        }
    }
}

@media screen and(min-height: 1200px){
    .registration-listnear{
        height: 100vh;
    }
}

@media (max-width: 992px) {
    .registration-listnear {
        .registration-listnear-wrapper {
            padding: 100px 80px 100px 0;
            .form-wrapper {
                display: flex;
                flex-direction: column;
                align-items: center;
                .form {
                    display: flex;
                    flex-direction: column;
                    width: 90%;
                    .form-first,
                    .form-last {
                        width: 100%;
                        padding: 0 10px;
                        ul {
                            li {
                                input {
                                    width: 60%;
                                }
                                .category {
                                    width: 60%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .registration-listnear {
        .registration-listnear-wrapper {
            padding: 100px 35px 100px 0;
            .form-wrapper {
                .form {
                    .form-first,
                    .form-last {
                        ul {
                            li {
                                input {
                                    width: 55%;
                                }
                                .category {
                                    width: 55%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 576px) {
    .registration-listnear {
        .registration-listnear-wrapper {
            padding: 100px 0;
            .form-wrapper {
                .form {
                    .form-first,
                    .form-last {
                        ul {
                            li {
                                display: flex;
                                flex-direction: column;
                                .label {
                                    margin-bottom: 10px;
                                }
                                input {
                                    width: 100%;
                                }
                                .category {
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
                .button-submit {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding-top: 20px;
                }
            }
        }
    }
}
