.home {
  //background-color: #F4F8FF;
  padding-top: 80px;
  font-family: Montserrat, serif;

  .header-img {
    height: 85vh !important;
    background: url(../img/home.jpg) center/cover no-repeat;
    margin-bottom: 10px;

    button {
      margin: 480px 50%;
      transform: translateX(-50%);
      width: 360px;
      height: 70px;
      font-size: 24px;
      border-radius: 30px;
      background: linear-gradient(180deg, #222D44 0%, #4E5E80 100%);
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
      font-family: Montserrat;
      font-style: normal;
      font-weight: 500;
      line-height: 29px;
      color: #FAFAFA;
    }
  }

  .what-clinic {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;

    .what-clinic-text,
    .what-clinic-mini {
      min-height: 340px;
      border-radius: 10px;
      margin: 10px;
      background-color: white;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
      padding: 20px;
      font-size: 14px;
      p {
        line-height: 24px;
        margin: 10px;
      }
    }
    .what-clinic-text {
      width: 60%;
    }

    .what-clinic-mini {
      width: 35%;
    }

  }


  #cel-clinic,
  #adres-procuratura {
    height: 100%;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 15px 10px 30px 10px;
    font-size: 16px;
  }

  #cel-clinic {
    padding: 20px;

    h5 {
      margin: 20px;
      font-weight: 600;
      font-size: 18px;
      color: #222d44;
    }

    .cel-clinic-text {
      display: flex;
      flex-direction: row;

      .text-inform {
        width: 100%;
        padding-left: 25px;
        ul {
          li {
            list-style-type: disc;
            margin-bottom: 25px;
            line-height: 25px;
          }
        }
        .list_text{
          margin-bottom: 20px;
          display: flex;
          justify-content: start;
          align-items: center;
          span{
            line-height: 25px;
          }
          img{
            width: 15px;
            height: 15px;
            margin-right: 20px;
          }
        }
      }
      .ml-10{
        margin-left: -20px !important;
      }
    }
  }

  #statistic-clinic {
    width: 100%;
    .statistic-row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 80%;
      margin: auto;
      .statistic-items {
        margin-top: 11px;
        .statistic {
          min-height: 300px;
          min-width: 350px;
          align-items: center;
          justify-content: center;
          text-align: center;
          border-radius: 10px;
          padding: 85px 10px;
          background-color: rgba(255,255,255,0.8);
          box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
          margin: 0 8px;
          color: white;

          h3 {
            font-size: 70px;
            font-weight: 600;
            margin-bottom: 10px;
          }

          p {
            line-height: 24px;
            font-size: 21px;
          }
        }
      }
    }
  }

  #purpose-clinic {
    min-height: 99%;
    margin: 15px 0px 20px 0px;

    .porpose-text {
      height: 100%;
      border-radius: 10px;
      background-color: white;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
      margin-top: 20px;
      padding: 15px;
      font-size: 16px;

      p {
        line-height: 25px;
      }
    }
  }

  #popular-clinic {
    min-height: 420px;
    margin: 15px 10px 20px 10px;
  }

  #legal-clinic {
    min-height: 400px;
    margin: 15px 10px 20px 10px;

    .legal-body {
      display: flex;
      flex-direction: row;
      padding-top: 30px;

      .legal-body-items {
        width: 50%;
        margin: 0 15px;

      }
    }
  }

  #home-useful-links {
    //background-color: #F4F8FF;
    min-height: 380px;
    padding: 20px;
    margin-bottom: 20px;
    display: flex;

    .useful-links-body {

      display: flex;
      flex-direction: row;
      min-height: 280px;
      margin-top: 20px;
      justify-content: center;
      flex-wrap: wrap;

      .useful-links-item {
        background-color: white;
        //height: 300px;
        width: 350px;
        height: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;
        margin: 10px 10px;
        padding: 90px 10px;
        border-radius: 20px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        transition: 0.7s all ease;

        &:hover {
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
        }

        img {
          height: 75px;
          width: 75px;
          margin: 20px 0 20px 0;
        }

        p {
          text-align: center;
          line-height: 20px;
          font-weight: 600;
          margin-bottom: 10px;
        }
      }

      .useful-links-item-regulatory{
        width: 340px;
      }
    }

    .useful-links-body1 {

      // display: flex;
      // flex-wrap: wrap;
      min-height: 280px;
      margin-top: 20px;
      // justify-content: center;

      .useful-links-item1 {
        background-color: white;

        height: 200px;
        // width: 300px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        cursor: pointer;
        margin: 10px 10px;
        padding: 10px 30px;
        border-radius: 20px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        transition: 0.7s all ease;

        &:hover {
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
        }

        img {
          width: 25%;
        }

        p {
          border-left: 1px solid rgba(0,0,0,0.2);
          text-align: left;
          height: 70%;
          display: flex;
          justify-content: center;
          align-items: center;
          padding-left: 15px;
          width: 75%;
          line-height: 20px;
          font-weight: 600;
        }
      }
    }
  }
}

