.reqover-account {
    background-color: #f4f8ff;
    padding: 100px 0;
    .container {
        .form {
            height: 320px;
            width: 620px;
            background-color: #ffffff;
            box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            margin: 0 auto;
            margin-top: 20px;
            padding: 30px;
            color: #222d44;
            h4 {
                text-align: center;
                font-weight: 600;
                font-size: 22px;
                line-height: 150%;
                color: #222d44;
                margin-bottom: 20px;
            }
            form {
                display: flex;
                flex-direction: column;
                align-items: center;
                .form-password {
                    display: flex;
                    flex-direction: column;
                    margin-bottom: 30px;
                    .label{
                        margin-bottom: 20px;
                    }
                    input {
                        width: 290px;
                        height: 40px;
                        background-color: #ffffff;
                        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
                        border-radius: 10px;
                        padding: 20px;
                    }
                }

            }
        }
    }
}
