.content-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  background: #ffffff;
  border-radius: 10px;
  margin-left: 30px;
  padding: 15px;

  .content-top {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 20px;
    flex-wrap: wrap;

    .request-items {
      max-width: 260px;
      height: 40px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      background-color: #ffffff;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
      border-radius: 30px;

      .category {
        border-radius: 30px;
      }

      p {
        margin: 0 5px;
      }

      .reuest-item-number {
        font-weight: bold;
        font-size: 18px;
        color: #222d44;
      }
    }

    .request-search {
      min-width: 150px;
      height: 40px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      border-radius: 30px;
      background: #ffffff;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
      padding: 0 25px;

      .search-form {
        display: flex;
        flex-direction: row;
      }

      input {
        height: 40px;
        width: 100%;
      }

      button {
        background: #ffffff;

        img {
          height: 20px;
          width: 20px;
        }
      }
    }

    .request-sort {
      display: flex;
      flex-direction: row;
      align-items: center;

      label {
        font-size: 12px;
      }

      select {
        max-width: 125px;
        height: 40px;
        background-color: #ffffff;
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
        border-radius: 30px;
        margin-left: 10px;
        border: none;
        padding: 10px 10px;
        font-size: 14px;
        font-weight: bold;
      }
    }
  }

  .new {
    margin-top: -5px;

    .new-item {
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      height: 20px;
      width: 20px;
      border-radius: 50%;
      background-color: red;
      font-size: 10px;
      font-weight: 500;
      position: relative;
      top: 10px;
      left: 98.5%;
    }
  }

