  .content {
    background-color: #ffffff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 20px 15px;
    margin-bottom: 15px;

    //height: 100px;
    // &:last-child {
    //     margin: 0;
    // }
    .user-item {
      min-height: 50px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      // margin-bottom: 20px;
      .user-person-inform {
        display: flex;
        flex-direction: row;
        align-items: center;

        .user-img {
          height: 55px;
          width: 55px;
          border-radius: 50%;
          background-color: green;
          margin-right: 10px;
        }

        .user-inform {
          .user-name {
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;
            color: #000000;
            margin-bottom: 5px;
          }

          .user-porofeesion {
            font-size: 14px;
          }
        }
      }
    }

    .content-line {
      height: 1px;
      background-color: black;
      margin: 30px 0;
    }

    .document-prosses {
      height: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      //margin: 10px 0 ;
      //padding: 5px;

      .new,
      .progressing,
      .close {
        h3 {
          font-size: 12px;
          text-align: center;
        }

        width: 120px;
        color: #9096a1;
      }

      .progressing {
        h3 {
          width: 120px;
          font-size: 12px;
          text-align: center;
        }
      }

      align-items: center;

      div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h3 {
          font-size: 12px;
          font-weight: bolder;
          color: #9096a1;
          margin-bottom: 5px;
        }
      }

      .prosses-item {
        height: 20px;
        width: 20px;
        border-radius: 50%;
        border: 1px solid #78909C;

      }

      .prosses-item-active {
        height: 15px;
        width: 15px;
        border-radius: 50%;
        background-color: #78909C;
      }

      .line {
        height: 2px;
        width: 30%;
        margin-top: 30px;
        background-color: #222d44;
      }
    }

    .document-text {
      margin-top: 20px;

      .document-text-title {
        display: flex;
        flex-direction: row;
        margin-bottom: 10px;

        h4 {
          font-size: 20px;
          font-weight: 500;
          color: #000000;
        }

        p {
          font-size: 18px;
          color: #222d44;
          margin-left: 10px;
        }
      }

      .document-text-item {
        font-size: 14px;
        line-height: 24px;
        color: #222d44;
      }
    }

    .request-theme {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin: 15px 0;
      flex-wrap: wrap;

      h3 {
        font-weight: 500;
        font-size: 20px;
        color: #000000;
        margin-bottom: 10px;
      }

      span {
        color: grey;
        margin-left: 10px;
      }

      input {
        margin-right: 10px;
      }
    }

    .request-content-item {
      font-size: 14px;
      line-height: 24px;
      color: #222d44;
      margin-bottom: 20px;

      p {
        word-break: break-word;
        text-align: justify;
      }
    }

    .request-content-title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      position: relative;

      .request-content-title-date {
        display: flex;
        flex-direction: row;
        align-items: center;
        position: absolute;
        top: 0;
        right: 0;
        .date-item {
          padding: 6px 18px;
          border-radius: 30px;
          color: white;
          margin-left: 10px;
        }

        button {
          border: 2px solid #000000;
          border-radius: 30px;
          padding: 6px 10px;
          background-color: white;
          color: #000000;
          font-weight: 500;
          margin-left: 10px;
        }
      }
    }

    .message-content {
      padding: 15px 0;
      margin: 10px 0;
      min-height: 250px;
      max-height: 350px;
      overflow: auto;
      position: relative;
      background-color: #DCF8C6;
      max-width: 100%;

      .messaging-day {
        padding-top: 16px;
        padding-bottom: 16px;
        text-align: center;
        display: block;
        clear: both;

        span {
          background-color: rgba(0, 0, 0, .1);
          padding: 6px 8px;
          border-radius: 10px;
        }
      }

      .message-paragrapgh {
        display: block !important;
        clear: both;
        position: relative;

        .action-message {
          background-color: #F4F8F0;
          color: white;
          padding: 4px;
          border-radius: 4px;
          display: none;
          margin: 5px -10px 0 0;

          span {
            cursor: pointer;
          }

          .message-edit {
            color: #3c90df;

            &:hover {
              text-decoration: underline;
              color: #3c90df;
            }
          }

          .message-delete {
            color: red;

            &:hover {
              color: red;
            }
          }
        }

        p {
          width: 70%;
          font-size: 18px;
          padding: 10px 5px;
          border-radius: 12px;
          background-color: #F4F8FF;
          margin: 5px 7px 4px;
          position: relative;
          line-height: 27px;
          display: inline-block;

          .message {

          }

          .icon {
            display: inline-block;
            float: right;
            margin-left: 5px;

            img {
              margin-left: 5px;
            }
          }
        }

        .me {
          float: right;
          background-color: #F4F8F0;

          .message {
            word-break: break-all;
          }
        }
      }

    }

    .message-paragrapgh:hover {
      .action-message {
        display: inline-block;
      }
    }

    .message-input {
      width: 100%;
      height: 60px;
      margin: 10px 0;
      position: relative;

      .clear-edit {
        position: relative;
        float: right;
      }

      .clear-edit:hover {
        background-color: rgba(0, 0, 0, 0.5);
        color: white;
      }

      textarea {
        border-radius: 10px;
        font-size: 18px;
        background-color: #F4F8FF;
        width: 100%;
        //margin: 10px;
        //padding: 10px;
        padding-right: 40px;
        padding-left: 10px;
        padding-top: 10px;
        min-height: 50px;
        max-height: 75px;
        float: right;

      }

      span {
        top: 10px;
        right: 8px;
        position: absolute;
        cursor: pointer;
      }
    }

    .message-hours {
      width: 100%;
      margin-bottom: 10px;

      .hours {
        width: 100%;
        height: 25px;
        float: right;
        text-align: right;
        margin-right: 10px;
        font-size: 18px;
      }
    }

    .fedbeck {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      position: relative;

      .hours {
        position: absolute;
        bottom: -20px;
        right: 10px;
      }

      .fedbeck-right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        position: relative;

        .redaction-date {
          margin-left: auto;
          margin-top: 10px;
          padding-left: 35px;

          a {
            color: black;
          }
        }

        .btn-default {
          font-size: 16px;
          padding: 7px 10px;
          margin-bottom: 5px;
          width: 90px;
          height: 40px;
        }
      }

      .supervisor-applicants {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        h5 {
          font-size: 18px;
          font-weight: 500;
          margin-bottom: 10px;
        }

        strong {
          margin-left: 10px;
        }

        .departmens {
          display: flex;
          flex-direction: column;
          align-items: flex-end;

          h6 {
            margin-bottom: 10px;

            strong {
              margin-left: 10px;
              font-weight: 500;
            }
          }
        }
      }

      .count-request {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-bottom: 10px;
        font-size: 16px;

        strong {
          margin-left: 10px;
          font-size: 18px;
        }
      }

      .fedbeck-red-btn {
        padding: 5px 10px;
        border-radius: 30px;
        border: 2px solid #ff4500;
        color: #ff4500;
        background-color: white;
      }

      .fedbeck-green-btn {
        padding: 5px 10px;
        border-radius: 30px;
        border: 2px solid #63aa55;
        color: #63aa55;
        background-color: white;
      }
    }

    .file-upload {
      align-items: center;
      margin-bottom: 30px;

      label {
        margin-right: 20px;
      }
    }

    .fedbeck-content {
      margin-top: 20px;

      p {
        margin: 15px 0;
        line-height: 20px;
      }
    }

    .button-white {
      padding: 5px 10px;
      border-radius: 30px;
      border: 2px solid black;
      background: white;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    }

    .category-audio {
      height: 100px;
      background-color: #b3c6ff;
      margin-bottom: 20px;
    }

    .request-bottom {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 15px;

      a {
        font-weight: 500;
        font-size: 16px;
        text-decoration-line: underline;
        color: #242f47;
        margin-bottom: 10px;
      }

      button {
        margin-left: 10px;
      }
    }

    .answer-score {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      height: 50px;

      h4 {
        font-size: 16px;
        font-weight: 600;
      }

      .answer-score-button {
        display: flex;
        flex-direction: row;

        button {
          background-color: white;
          padding: 7px 15px;
          border-radius: 20px;
          border: 2px solid black;
          color: black;
          font-weight: normal;
          margin: 0 10px;
        }
      }
    }
  }

