@media (max-width: 768px) {
  .content-wrapper {
    margin-left: 0;

    .content-top {
      justify-content: center;

      .request-items, .request-search {
        margin: 5px;
        width: 200px;

        .category {
          width: 200px;
        }
      }
    }

    ::-webkit-scrollbar {
      height: 20px;
    }

    ::-webkit-scrollbar-track {
      background: #ececec;
      box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      padding: 10px;
    }

    ::-webkit-scrollbar-thumb {
      height: 10px;
      width: 100px;
      background: #bdbaba;

      border-radius: 10px;
    }

    .statistic-section-item {
      &-body {
        overflow-x: auto;
        position: relative;
      }

      .statistic-regions {
        .statistic-section-item-body {
          height: 800px;
        }
      }
    }
  }
  .content-wrapper .content .message-content .message-paragrapgh p {
    font-size: 14px;
    padding: 7px 5px;
    line-height: 18px;
    border-radius: 8px;

    .icon {
      font-size: 10px;

      img {
        font-size: 10px;
        width: 15px;
        height: 15px;
      }
    }
  }
  .content-wrapper .content .message-content .message-paragrapgh .action-message {
    .message-delete,.message-edit {
      svg {
        width: 15px;
        height: 15px;
      }
    }
  }
  .content-wrapper .content .message-content{
    padding: 5px 0;
    .messaging-day{
      padding: 10px 0;
      span{
        font-size: 12px;
      }
    }
  }

}
@media (max-width: 481px) {

  .content-wrapper .content .message-content .message-paragrapgh p {
    font-size: 12px;
    padding:5px;
    line-height: 16px;
    .icon {
      font-size: 8px;

      img {
        font-size: 8px;
        width: 12px;
        height: 12px;
      }
    }
  }
  .content-wrapper .content .message-content{
    padding: 5px 0;
    .messaging-day{
      padding: 9px 0;
      span{
        padding: 4px 6px;
        font-size: 10px;
      }
    }
  }
}