.chart_map_container {
  max-height: 40vh;
}

.map_chart {
  position: relative;
  padding-bottom: 0;
  margin-bottom: 0;


  .regions {
    cursor: pointer;

    &:hover {
      fill: #1890ff !important;
    }
  }

  .regions_active {
    fill: #1890ff !important;
    cursor: pointer;
  }
}

.region_data_detail {
  position: relative;

  .close_icon {
    position: relative;
    top: 0;
    float: right;
    color: red;
    cursor: pointer;
  }
}

.list_region {
  &:hover {
    background-color: #f5f5f5;
    cursor: pointer;
  }
}

.map-container {
  position: relative;

  div {
    overflow: hidden;
    .description {
      position: absolute;
      right: 0;
      width: 450px;
      z-index: 1;
    }

    .description1{
      display: none;
    }

    div {
      z-index: 0;
      svg {
        padding-top: 35px;
      }

    }
  }
}

@media screen and(max-width: 1045px){
  .map-container{
    div{
      .description{
        top: -20px;
      }
    }
  }
}

@media screen and(max-width: 950px){
  .map-container{
    div{
      .description{
        display: none;
      }

      .description1{
        display: block;
      }
    }
  }
}

@media screen and(max-width: 720px){
  .map-container{
    div{

      .description1{
        display: block;
        bottom: 0;
        div{
          h1{
            font-size: 15px!important;
          }
        }
      }
    }
  }
}