.address-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  .address-section-1{
    width: 575px;

    .section-item{

      padding: 15px 12px;
      .section-item-card{
        min-height: 380px;
        box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
        padding: 15px;
        display: flex;
        flex-wrap: wrap;
        border-radius: 10px;
        transition: box-shadow 700ms ease;
        &:hover{
          box-shadow: 0 0 5px 0 rgba(0,0,0,0.5);
        }
        //list-style-type: circle;
        .item-card-img{
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          img{
            position: relative;
            width: 100px;
          }
        }
        .item-card-content{
          width:100%;
          .name-place{
            font-weight: bold;
            text-align: center;
            text-transform: uppercase;
            line-height: 20px;
            font-family: Montserrat, sans-serif;
          }
          .address-place{
            display: flex;
            justify-content: center;
            align-items: center;
            line-height: 35px;
          }
          .place-actions{
            .places{
              padding: 10px;
              display: flex;
              flex-wrap: wrap;
              align-items: center;
              justify-content: center;

              .attr{
                width: 190px;
                display: flex;
                justify-content: center;
                align-items: center;
              }
            }
            .social-sites{
              padding: 10px;
              display: flex;
              align-items: center;
              justify-content: center;
              a{
                margin: 5px;
                padding: 10px;
                border: 1px solid rgba(0,0,0,0.1);
                border-radius: 6px;

              }
            }
          }
        }
      }
    }
  }

